import React from 'react';

export class Temp extends React.Component {
constructor(props){
  super(props);
  this.state={vRan:[0,0,0,0],Winner:0};
  
}
MathRandom(i,j)
{
  var lRan=[];
  lRan[0]=Dice();
  lRan[1]=Dice();
  lRan[2]=i;
  lRan[3]=j;
  if(intEqual(lRan[0],lRan[3])&&!intEqual(lRan[1],lRan[2]))
  {
    this.setState({Winner:2});
  }
  else if(intEqual(lRan[1],lRan[2])&&!intEqual(lRan[0],lRan[3]))
  {
    this.setState({Winner:1});
  }
  else
  {
    this.setState({Winner:0});
  }
  lRan[1]+=lRan[0];
  lRan[3]+=lRan[2];
  this.setState({vRan:lRan});
}
  render()
  {
    const total=this.state.vRan[0]+this.state.vRan[2];
    var myWin;
    var sw1;
    var sw2;
    if(this.state.Winner>1.1)
    {
      sw1="pplrr";
      sw2="pplrg";
      myWin=<h2>Winner:Hero</h2>
    }
    else if(this.state.Winner>0.1)
    {
      sw2="pplrr";
      sw1="pplrg";
      myWin=<h2>Winner:Villain</h2>
    }
    else
    {
      sw1="pplr";
      sw2="pplr";
      myWin=<h2>No winner</h2>
    }
    return (<div>
    <div className="gtitle">
        <h1>finger-guessing game</h1>
    </div>
    <div className="pgame">
      <Player name="Villain" fns={this.state.vRan[0]} ttl={this.state.vRan[1]} shwin={sw1}></Player>
      <div className="pwin">
            <h2>{myWin}</h2>
          <h3>{total}</h3>
      </div>
      <Player name="Hero" fns={this.state.vRan[2]} ttl={this.state.vRan[3]} shwin={sw2}></Player>

      <button onClick={(i,j)=>this.MathRandom(0,0)}>H0T0</button>
      <button onClick={(i,j)=>this.MathRandom(0,1)}>H0T1</button>
      <button onClick={(i,j)=>this.MathRandom(0,2)}>H0T2</button>
      <button onClick={(i,j)=>this.MathRandom(0,3)}>H0T3</button>
      <button onClick={(i,j)=>this.MathRandom(0,4)}>H0T4</button>
      <button onClick={(i,j)=>this.MathRandom(0,5)}>H0T5</button><br/>
      <button onClick={(i,j)=>this.MathRandom(1,0)}>H1T1</button>
      <button onClick={(i,j)=>this.MathRandom(1,1)}>H1T2</button>
      <button onClick={(i,j)=>this.MathRandom(1,2)}>H1T3</button>
      <button onClick={(i,j)=>this.MathRandom(1,3)}>H1T4</button>
      <button onClick={(i,j)=>this.MathRandom(1,4)}>H1T5</button>
      <button onClick={(i,j)=>this.MathRandom(1,5)}>H1T6</button><br/>
      <button onClick={(i,j)=>this.MathRandom(2,0)}>H2T2</button>
      <button onClick={(i,j)=>this.MathRandom(2,1)}>H2T3</button>
      <button onClick={(i,j)=>this.MathRandom(2,2)}>H2T4</button>
      <button onClick={(i,j)=>this.MathRandom(2,3)}>H2T5</button>
      <button onClick={(i,j)=>this.MathRandom(2,4)}>H2T6</button>
      <button onClick={(i,j)=>this.MathRandom(2,5)}>H2T7</button><br/>

      <button onClick={(i,j)=>this.MathRandom(3,0)}>H3T3</button>
      <button onClick={(i,j)=>this.MathRandom(3,1)}>H3T4</button>
      <button onClick={(i,j)=>this.MathRandom(3,2)}>H3T5</button>
      <button onClick={(i,j)=>this.MathRandom(3,3)}>H3T6</button>
      <button onClick={(i,j)=>this.MathRandom(3,4)}>H3T7</button>
      <button onClick={(i,j)=>this.MathRandom(3,5)}>H3T8</button><br/>
      <button onClick={(i,j)=>this.MathRandom(4,0)}>H4T4</button>
      <button onClick={(i,j)=>this.MathRandom(4,1)}>H4T5</button>
      <button onClick={(i,j)=>this.MathRandom(4,2)}>H4T6</button>
      <button onClick={(i,j)=>this.MathRandom(4,3)}>H4T7</button>
      <button onClick={(i,j)=>this.MathRandom(4,4)}>H4T8</button>
      <button onClick={(i,j)=>this.MathRandom(4,5)}>H4T9</button><br/>
      <button onClick={(i,j)=>this.MathRandom(5,0)}>H5T5</button>
      <button onClick={(i,j)=>this.MathRandom(5,1)}>H5T6</button>
      <button onClick={(i,j)=>this.MathRandom(5,2)}>H5T7</button>
      <button onClick={(i,j)=>this.MathRandom(5,3)}>H5T8</button>
      <button onClick={(i,j)=>this.MathRandom(5,4)}>H5T9</button>
      <button onClick={(i,j)=>this.MathRandom(5,5)}>H5T10</button><br/>

    </div>
    <div>
      <ul>
        <li>2 players: villain and hero</li>
        <li></li>
      </ul>
    </div>
    </div>);
  }
}

class Player extends React.Component{
  render(){
    return(<div className={this.props.shwin}>
        <h1>{this.props.name}</h1>
        <h1>{this.props.ttl}</h1>
        <h3>{this.props.fns}</h3>
    </div>);
  }
}
function Dice()
{
  return  Math.floor(Math.random()*6);
}
function intEqual(v1,v2)
{
   if(Math.abs(v1-v2)>0.1) return false;
   return true;
}
